@import url(https://fonts.googleapis.com/css2?family=Mulish:ital@1&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  word-wrap: break-word;
  font-family: "Mulish", sans-serif;
}

/* Serviec.jsx css here*/
.media_hover:hover {
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1) inset,
    3px 3px 5px rgba(0, 0, 0, 0.2);
  color: #ff0000;
  cursor: pointer;
}
.mt-0:hover {
  color: blue;
}
/*Contact.jsx css here*/
.twitter {
  background-color: #55acee;
  color: #fff;
  outline: none;
  border: none;
  padding: 6px 23px;
}
.facebook {
  background-color: #3b5999;
  color: #fff;
  outline: none;
  margin-left: 10px;
  border: none;
  padding: 6px 23px;
}
.instagram {
  background-color: rgb(233, 50, 50);
  color: #fff;
  outline: none;
  margin-left: 10px;
  border: none;
  padding: 6px 23px;
}
/* Laptop.jsx css here */
.col-md-3:hover {
  transform: translateX(px);
  transition: all 0.4s ease-in;
  cursor: pointer;
}
/* ExclusiveRef.jsx product css here*/
.offersbackground {
  background: #11998e; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #38ef7d,
    #11998e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media only screen and (max-width: 800px) {
  .collection {
    height: 90px;
  }
}

.fa-bars {
  margin-top: 10px;
  margin-right: 20px;
  display: none;
  cursor: pointer;
}
.active_class {
  background-color: #e4141ed7;
}
.fa-times {
  display: none;
}
.usershow {
  display: none;
}
@media only screen and (max-width: 1200px) {
  .form-inline {
    display: block;
  }
  .cartplus > .fa-cart-plus,
  .cartheart > .fa-heart {
    display: none;
  }

  .form-inline > .form-control,
  .form-inline > .btn {
    width: 130px;
    margin-top: -50px;
    margin-left: 10px;
  }
  .fa-times {
    display: inline-block;
    position: absolute;
    top: 18px;
    left: 85%;
    z-index: 700;
    cursor: pointer;
  }
  .fa-times:hover {
    color: red;
  }
  .usershow {
    display: inline-block;
  }
  .conpanyname {
    font-size: 24px;
    padding-left: 2px;
    margin-top: 10px;
  }
  .updatetime {
    position: absolute;
    top: 23px;
    left: 48%;
    font-size: 12px;
    padding: 0 5px;
  }

  .nav {
    position: absolute;
    top: 8px;
    /* height: 500px; */
    height: 90vh;
    background: #000000e3;
    width: 59%;
    overflow: hidden;

    display: none;

    z-index: 50;
  }
  .fa-cart-plus,
  .nav > .nav-link {
    font-size: 16px;
  }
  .fa-bars {
    display: block;
    margin-right: 12px;
  }
  .nav-link > span:hover {
    color: rgb(209, 192, 192);
  }
}
@media only screen and (max-width: 1000px) {
  #hide {
    max-height: 110px;
  }
}
@media only screen and (max-width: 700px) {
  .banner {
    height: 35vh;
  }
}

.itemhover:hover {
  transform: translateY(-8px);
  cursor: pointer;
}
.itemhover {
  transition: all 0.5s ease-in;
}

.col-md-2 > ul > li {
  margin: 10px 0;
  padding-left: 10px;
  list-style: none;
  font-size: 18px;
}
.col-md-2 {
  max-height: 200vh;
  overflow: auto;
  scroll-behavior: smooth;
}

